.home-section-featured {
	font-family: $font-family-heading;
	.section-post-media {
		width: auto;
	}

	.section-post-title {
		font-size: 14px;
	}
	.section-post-excerpt {
		font-size: 10px;
		float: left;
		margin-right: 10px;
		line-height: 1.7em;
	}

	.section-post-price {
		font-size: 24px;
		font-weight: 700;
	}

	.rating {
		float: left;
	}
}
@media screen and (max-width: 991px) {
	.home-section-featured {
		text-align: center;
		.section-post-excerpt {
			float: none;
			margin-right: 0;
		}
		.rating {
			float: none;
		}
	}
}