.saleoff-actions{
	position: relative;
	font-family: $font-family-heading;
	margin-top: 60px;
	.amount {
		font-size: 30px;
		font-weight: 700;
		color: $awe-primary-color;
		margin-right: 10px;
	}
	del.amount {
		font-size: 18px;
		font-weight: 400;
		color: #898989;
	}
	h4 {
		color: #898989;
		text-transform: uppercase;
		margin-top: 30px;
		margin-bottom: 15px;
	}
	p {
		font-family: $font-family-base;
		color: #898989;
		margin-bottom: 20px;
	}
}
@media screen and (max-width: 991px) {
	.saleoff-actions {
		margin-top: 0;
	}
}